body {
  background-color: #ccc;
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}

.page {
  position: relative;
  text-align: center;
}

.page__search {
  padding-top: 10px;
}

.page__error {
  position: absolute;
  padding: 5px;
  color: #fff;
  background-color: #f44336;
}

.pokemons {
  margin: 0;
  padding: 0;
  list-style: none;
}

.pokemons__item {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin: 10px;
  padding:0px;
}

.pokemon {
  position: relative;
  z-index: 20000;
  overflow: hidden;
  background-color: #eee;
  cursor: pointer;
}

.pokemon__sprite {
  background-color: transparent;
  background-position: center center;
  background-repeat: no-repeat;
}

.pokemon,
.pokemon__sprite {
}

.pokemon__name {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 20;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  font-size: 14px;
  pointer-events: none;
}

.re-card{
   width: 400px;
    background-color: white;
    padding: 3%;
    border-radius: 0px;
    -webkit-box-shadow: 0px 2px 25px #a7a7a7;
            box-shadow: 0px 2px 25px #a7a7a7;
}

.edit-btn{
  float: right;
  /* background-color: #00a4ff; */
  background: none;
  background-color: #dc5038;
  border: none;
  outline: none;
  border-radius: 0px;
}

.indi-box{
  padding-top:3%;
}

.searchbox{
  border-radius: 0px;
    width: 81%;
    -ms-flex-align: center;
        align-items: center;
    display: inline-block;
}

.indi-list{
  text-align: left;
  padding-top: 11%;
  font-size: 18px;
  padding-left:0;
}

.rating{
  float: left;
    font-weight: 800;
    color: #a9a9a9;
    margin-top: 5px;
}

.seemore{
  position: absolute;
  bottom: 3%;
  text-align: center;
  color: #6d6d6d;
  right: 6%;
}

.indi-title{
  background-color: #6b6b6b;
  padding: 3%;
  font-size: 20px;
  color: white;
  text-align: left;
  border-bottom-left-radius: 0px;
  padding-left: 4%;
}

.indi-quant{
  float: right;
  color: #ffffff82;
  margin-right: 3%;
  font-size: 13px;
  margin-top: 3px;
}

.indi-item{
  background-color: #dc5038;
  border-radius: 0px;
  /* padding-left: 10px; */
  color: white;
  text-align: center;
  padding: 2%;
  font-size: 17px;
  margin-top: 3px;
  text-align: left;
  padding-left: 4%;
}

@media (max-width: 700px) {
  .pokemon,
  .pokemon__sprite {
    
  }
  .re-card{
    width:67%;
  }
}



